img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
.top-line {
  color: #f8b260;
  display: flex;
  justify-content: space-between;
}
.top-line__header {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  position: relative;
  margin-left: 38px;
  margin-bottom: 34px;
}
.top-line__header::after {
  position: absolute;
  content: "";
  width: 24px;
  height: 24px;
  border: 1px solid #f8b260;
  border-radius: 50%;
  top: -2px;
  left: -38px;
}
.top-line__header::before {
  position: absolute;
  content: "";
  width: 12px;
  height: 12px;
  background-color: #f8b260;
  border-radius: 50%;
  top: 4px;
  left: -32px;
}
.top-line__link {
  color: #414141;
  text-decoration: underline;
  height: 20px;
}
.top-line__link:hover {
  color: #c4c4c4;
}
.background-content {
  width: 865px;
  background-color: #1a1a1a;
  padding-top: 28px;
  padding-bottom: 30px;
  margin-bottom: 30px;
}
@keyframes slidein {
  0% {
    transform: rotate(45deg);
  }
  50% {
    transform: rotate(-45deg);
  }
  100% {
    transform: rotate(45deg);
  }
}
