}
.button__notifications-count {
  display: none;
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 3px;
  background-color: #f32a2a;
  color: #e5e5e5;
  font-size: 11px;
  line-height: 14px;
  text-align: center;
  top: -4px;
  right: -2px;
