}
.slider {
  width: 800px;
  height: 400px;
  position: relative;
  margin: 0 auto;
  overflow: hidden;
}
.slider__controls {
  position: absolute;
  bottom: 24px;
  right: 24px;
  z-index: 1;
}
.slider__controls-button {
