  overflow: hidden;
}
.itcss__items {
  display: flex;
  transition: transform 0.5s ease;
}
.itcss__item {
  flex: 0 0 100%;
  max-width: 100%;
  position: relative;
}
.itcss__indicators {
  position: absolute;
  bottom: 24px;
  right: 24px;
  display: flex;
  justify-content: center;
  padding-left: 0;
  list-style: none;
  user-select: none;
}
.itcss__indicator {
  flex: 0 1 auto;
  width: 20px;
  height: 5px;
  margin-right: 5px;
  margin-left: 5px;
  background-color: #414141;
  background-clip: padding-box;
  box-sizing: content-box;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  cursor: pointer;
}
.itcss__indicator_active {
  background-color: #f8b260;
}
