.popup-auth {
  display: none;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.55);
}
.popup-auth__close {
  width: 25px;
  height: 25px;
  border: none;
  position: absolute;
  top: 0;
  right: 0;
  color: #c4c4c4;
  background-color: transparent;
}
.popup-auth__close:hover {
  color: #e5e5e5;
}
.popup-auth__wrapper {
  position: fixed;
  top: 15%;
  width: 800px;
  height: 540px;
  background: #212121 url("/img/register-form.png") no-repeat 480px 0px;
  z-index: 2;
  padding: 30px 40px;
}
.popup-auth--active {
  display: flex;
}
.popup-auth__header {
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: #e5e5e5;
}
.popup-auth__header-login {
  grid-column: 0.5;
}
.popup-auth__header-register {
  grid-column: 0.66666667;
}
.popup-auth__button-top {
  padding: 10px 15px;
  width: 170px;
  height: 40px;
  background-color: #414141;
  color: #c4c4c4;
}
.popup-auth__button-top-login {
  grid-column: 0.5;
}
.popup-auth__button-top-register {
  grid-column: 0.66666667;
}
.popup-auth__button-top:hover {
  background: linear-gradient(to bottom, #838383, #414141);
}
.popup-auth__form {
  width: 400px;
  display: none;
  justify-content: space-between;
  align-items: center;
  grid-template-columns: 190px 190px;
  grid-row-gap: 20px;
  grid-template-rows: 60px 70px 70px 70px 95px 10px;
}
.popup-auth__form--active {
  display: grid;
}
.popup-auth__label {
  display: block;
  color: #414141;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
}
.popup-auth__label-login {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 3;
}
.popup-auth__label-pass {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
}
.popup-auth__label-code {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 4;
  grid-row-end: 5;
}
.popup-auth__label-repeat-pass {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 4;
  grid-row-end: 5;
}
.popup-auth input {
  margin-top: 8px;
  display: block;
  background-color: #414141;
  border: none;
  height: 40px;
  width: 380px;
  box-sizing: border-box;
  padding: 5px 10px;
  color: #c4c4c4;
  font-size: 16px;
}
.popup-auth #code-in {
  width: 190px;
}
.popup-auth__code {
  width: 170px;
  height: 65px;
  background-color: #c4c4c4;
}
.popup-auth__button-in {
  width: 190px;
  height: 40px;
  grid-row-start: 5;
  grid-row-end: 6;
}
.popup-auth__button-in:hover {
  background: linear-gradient(to bottom, #fde9d3, #f8b260);
